    h1,h2.sub-content{
    display: inline-block;
    position: relative;
    }

    h2.sub-content:after {
    content: '';
    display: block;
    margin: auto;
    height: 3px;
    width: 0px;
    background: transparent;
    transition: 0.4s ease-out all .1s;
    background-color: .5s ease;
    }

    h2.sub-content:hover:after {
    width: 100%;
    background:#2a2f45;
    }

