    .line-style {
      display: inline-block;
      position: relative;
  }

  .line-style:after {
    content: '';
    display: block;
    margin: auto;
    height: 3px;
    width: 0px;
    background: transparent;
    transition: width .5s ease;
    background-color: .5s ease;
  }

  .line-style:hover:after {
    width: 100%;
    background:#2a2f45;
  }

